* {
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.header {
    background-color: #02203c;
    color: #00F97C;
    font-family: 'Poppins', sans-serif;
    position: relative;
}



.header__imageh {
    border-radius: 50%;
    margin-bottom: 5px;
}


.header__nameh:hover {
    transition-duration: 0.5s;
}

.header__desch {
    font-size: 35px;
}

.header__github-icon {
    cursor: pointer;
    margin-left: 5px;
}

.header__github-icon:hover {
    color: white;
    transition-duration: 5s;
}

.header__linkedin-icon {
    cursor: pointer;
    margin-left: 55px;
}

.header__linkedin-icon:hover {
    color:white;
    transition-duration: 5s;
}

.header__twitter-icon {
    cursor: pointer;
    margin-left: 55px;
    height: 55px;
}

.header__twitter-icon:hover {
    color: white;
    transition-duration: 5s;
}


.header__resume-icon {
    cursor: pointer;
    margin-left: 55px;
    height: 55px;
}
